<template>
  <div class="home-recommend">
    <div class="home-recommend-item" v-for="item in filterT4(cproducts)" :key="item.pict_url">
      <a :href="item.coupon_share_url">
        <img :src="item.pict_url" @load="finsihhomerecommendimgload" alt="">

      </a>
    </div>
  </div>


</template>

<script>
    export default {
        name: "HomeRecommend",
        data(){
          return{
            finishimgload: false
          }
        },
        props:{
            cproducts:{
                type:Array,
                default() {
                    return []
                }
            }
        },
        methods:{
            filterT4() {
                return this.cproducts.slice(0,4)
            },
            finsihhomerecommendimgload(){
              if (!this.finishimgload){this.$emit("finsihhomerecommendimgload")}
              this.finishimgload = true
            }
        }
    }
</script>

<style scoped>
  .home-recommend{
    display: flex;
    width: 100%;
    text-align: center;
    padding: 5px;
    border-bottom: 10px solid #eee;
  }

  .home-recommend-item{
    flex: 1;
  }

  .home-recommend-item img{
    width: 70px;
    height: 65px;
    border-radius: 100%;

  }
</style>
